.Input{
    width: 100%;
    padding: 10px; 
    box-sizing: border-box;
}

.Label{
    font-weight: bold;
    display: block;
    margin-bottom: 8px;
}

.InputElement{
    outline: none;
    border: 1px sold #ccc;
    font: inherit;
    background-color: white;
    padding: 6px 10px;
    width: 100%;
    display: block;
    box-sizing: border-box;
}

.InputElement:focus{
    outline: none;
    background-color: #ccc;
}

.Invalid{
    border: 1px solid red;
    background-color: #fda49a;
}

.ValidationError {
    color: red;
    margin: 5px 0;
    text-transform: capitalize;
} 